





































































































































































































































































































.select-nav {
  font-size: 28px;
  margin-top: -1px;
}

.tab {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 10vw;
  position: relative;
  color: #555;
  background: #fff;
  &::before {
    position: absolute;
    content: " ";
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #d8d8d8;
    transform-origin: 0 0;
    transform: scaleY(0.5);
  }
  .nav-item {
    width: 33.33%;
    flex-grow: 1;
    text-align: center;
    font-size: 28px;
    .title {
      display: inline-block;
      max-width: 80%;
    }
  }
}

.have-border {
  border-left: 1px solid #e6e6e6;
  border-right: 1px solid #e6e6e6;
}
.city-entry {
  padding-left: 30px;
  font-size: 30px;
  color: #666;
  display: flex;
  align-items: center;
  .city-entry-arrow {
    width: 0;
    height: 0;
    border: 8px solid #b0b0b0;
    border-left-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    display: inline-block;
    vertical-align: span-top;
    margin-left: 8px;
    margin-top: 10px;
  }
}

.select-item {
  color: #e54847;
  .city-entry-arrow {
    position: relative;
    transform: translateY(-8px);
    border: 8px solid #e54847;
    border-left-color: transparent;
    border-right-color: transparent;
    border-top-color: transparent;
  }
}

.nav-content {
  .nav-content-item {
    z-index: 999;
    position: relative;
    width: 100vw;
    height: calc(100vh * 0.6);
    background: #fff;
    color: #777;
  }
}
.region {
  .tab {
    border: none;
  }
  .nav-item {
    height: 100%;
    line-height: 80px;
    box-sizing: border-box;
    &.active {
      color: #e54847;
      border-bottom: 2px solid #e54847;
    }
  }
  .region-list {
    display: flex;
    width: 100%;
    height: calc(100% - 80px);
  }
}

.region-sidenav {
  white-space: nowrap;
  width: calc(100vw * 0.4);
  height: 100%;
  overflow: scroll;
  .side-item {
    height: 80px;
    line-height: 80px;
    padding-left: 30px;
    &.active {
      color: #dd403b;
      background: #f5f5f5;
    }
  }
}

.region-list-item {
  white-space: nowrap;
  flex-grow: 1;
  height: 100%;
  background: #f5f5f5;
  overflow: scroll;
  .item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 30px;
    padding-right: 50px;
    height: 80px;
  }
}

/* brand */

.brand-scroll-view {
  white-space: nowrap;
  width: 100%;
  height: 100%;
  overflow: scroll;
}

.brand-item {
  display: flex;
  justify-content: space-between;
  line-height: 88px;
  height: 88px;
  border-bottom: 1px solid #f0f0f0;
  padding-left: 45px;
  padding-right: 80px;
}

.red {
  color: #dd403b;
}

.brand-count {
  font-size: 24px;
}

/* special */

.special-scroll-view {
  white-space: nowrap;
  width: 100%;
  height: calc(100% - 120px);
}

.special {
  .item-title {
    padding: 20px 30px;
  }
  .item-list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 30px;
    .btn {
      width: 21.3%;
      height: 60px;
      margin-right: 3%;
      margin-bottom: 20px;
      line-height: 60px;
      text-align: center;
      border-radius: 10px;
      font-size: 24px;
      box-sizing: border-box;
      border: 1px solid #e5e5e5;
      padding: 0 6px;
      &.select {
        background: #fcf0f0;
        color: #f03d37;
        border: 1px solid #f03d37;
      }
    }
  }
}
.special-btn {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px;
  background: #fafafa;
  border-top: 1px solid #e5e5e5;
  padding: 0 30px;
  box-sizing: border-box;
  .btn {
    background: #fff;
    border: 1px solid #e5e5e5;
    height: 68px;
    width: 20%;
    text-align: center;
    border-radius: 12px;
    font-size: 28px;
    line-height: 68px;
    .confirm-btn {
      background: #f03d37;
      border: 1px solid #f03d37;
      color: #fff;
    }
  }
}
/* 遮罩层 */

.mask {
  position: fixed;
  z-index: -1;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
}
