
























































.cinema-section {
  padding: 30px 0;
  margin: 0 30px;
  border-bottom: 1px solid #f0f0f0;
  line-height: 1.3em;
  display: inline-block;
  width: 90%;
}

.name-price {
  font-size: 32px;
  color: #333;
  display: flex;
  justify-content: space-between;
}

.sell-price {
  font-size: 24px;
  color: #999;
  white-space: nowrap;
  margin-left: 20px;
}

.price {
  font-size: 34px;
  color: #ef4238;
}

.address {
  display: flex;
  justify-content: space-between;
  font-size: 28px;
  color: #666;
  line-height: 50px;
  .line-ellipsis {
    margin-right: 50px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}

.feature-tags {
  width: 80%;
  span {
    display: inline-block;
    border: 1px solid #589daf;
    color: #589daf;
    padding: 0 6px;
    height: 30px;
    line-height: 30px;
    border-radius: 4px;
    font-size: 24px;
    margin-right: 8px;
  }
  .featrue {
    color: #f90;
    border-color: #f90;
  }
  .card {
    position: relative;
    top: 6px;
    display: inline-block;
    width: 30px;
    height: 28px;
    margin-right: 6px;
    z-index: -1;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url("data:image/png;base64,");
  }
}
.discount-label-text {
  font-size: 22px;
  color: #999;
}
.showTimes {
  font-size: 24px;
  color: #999;
}
